export default {
	data() {
		return {
			type:''
		};
	},
	methods: {
		opennew: function(id) {
			uni.navigateTo({
				url: '../' + id + '/' + id
			});
		},
		opennew02: function(id) {
			uni.switchTab({
				url: '../' + id + '/' + id
			});
		},
		//流标
		liubiao: function(id) {
			uni.showModal({
				title: '温馨提示',
				content: '确定流标此求购单吗',
				confirmColor:'#008A05',
				success: res => {
					if (res.confirm) {
						console.log('用户点击确定');
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		// 展示求购单类型
		showtype:function(){
			uni.showActionSheet({
			    itemList: ['全部', '已过期', '审核中'],
			    success: function (res) {
			        console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
			    },
			    fail: function (res) {
			        console.log(res.errMsg);
			    }
			});
		}
	}
};
